    display: flex;
    align-items: center;
    gap: 10px;
    z-index: 50;
}

.visitor.leaving {
    animation: slideOut 1s ease-in-out forwards;
}

.avatar {
    font-size: 1.5em;
}

.visitor .avatar.bouncing {
    animation: visitorBounce 2s ease-in-out infinite;
}

@keyframes visitorBounce {
    0%, 100% { 
        transform: translateY(0);
    }
    50% { 
        transform: translateY(-8px);
    }
}

.message-bubble {
    background-color: white;
