        transform: translateY(-20px);
    }
}

.lane {
    position: absolute;
    width: 100%;
    height: 60px;
    background-color: rgba(30, 30, 30, 0.3);
    border-radius: 8px;
}

.lane:nth-child(1) { top: 15%; }
.lane:nth-child(2) { top: 35%; }
.lane:nth-child(3) { top: 55%; }

/*  */
.visitor {
    position: absolute;
    right: -100px;
    top: 20%;
    animation: slideIn 1s ease-in-out forwards;
    display: flex;
    align-items: center;
    gap: 10px;
    z-index: 50;
    filter: drop-shadow(0 4px 8px rgba(0,0,0,0.3));
}

.visitor.leaving {
    animation: slideOut 1s ease-in-out forwards;
}

.visitor .avatar {
    font-size: 1.5em;
    animation: bounce 2s ease-in-out infinite;
}

.visitor .message-bubble {
    background: linear-gradient(135deg, #ffffff 0%, #f0f0f0 100%);
    color: #333;
    padding: 8px 12px;
    border-radius: 15px;
    font-size: 12px;
    white-space: nowrap;
    box-shadow: 0 4px 15px rgba(0,0,0,0.2);
    max-width: 150px;
    overflow: hidden;
    text-overflow: ellipsis;
    position: relative;
}

.visitor .message-bubble::before {
    content: '';
    position: absolute;
    right: -8px;
    top: 50%;
    transform: translateY(-50%);
    border: 8px solid transparent;
    border-left-color: #ffffff;
}

/*  */
@keyframes slideIn {
    0% { 
        right: -200px; 
        opacity: 0;
        transform: translateY(20px) scale(0.8);
    }
    30% { 
        right: 40px; 
        opacity: 1;
        transform: translateY(-5px) scale(1.05);
    }
    100% { 
        right: 30px; 
        opacity: 1;
        transform: translateY(0) scale(1);
    }
}

@keyframes slideOut {
    0% { 
        right: 30px; 
        opacity: 1;
        transform: translateY(0) scale(1);
    }
    70% {
        right: 10px;
        opacity: 0.7;
        transform: translateY(-10px) scale(0.95);
    }
    100% { 
        right: -200px; 
        opacity: 0;
        transform: translateY(-20px) scale(0.8);
    }
}

/*  */
@keyframes sparkle {
    0%, 100% { opacity: 0; transform: scale(0) rotate(0deg); }
    50% { opacity: 1; transform: scale(1) rotate(180deg); }
}

.sparkle {
    position: absolute;
    width: 4px;
    height: 4px;
    background: #ffd700;
    border-radius: 50%;
    animation: sparkle 2s ease-in-out infinite;
}

.sparkle:nth-child(1) { top: 10%; left: 20%; animation-delay: 0s; }
.sparkle:nth-child(2) { top: 20%; right: 30%; animation-delay: 0.5s; }
.sparkle:nth-child(3) { bottom: 30%; left: 40%; animation-delay: 1s; }
.sparkle:nth-child(4) { bottom: 20%; right: 20%; animation-delay: 1.5s; }

/*  */
@media (max-width: 400px) {
    .setup-container {
        padding: 20px;
        margin: 10px;
