        flex-direction: column;
        gap: 10px;
        padding: 15px 10px;
    }
    
    .user-info {
        font-size: 11px;
        gap: 10px;
    }
    
    .self-avatar {
        font-size: 1.5em;
    }
    
    .visitor .message-bubble {
        max-width: 120px;
        font-size: 11px;
    }
}

