Cb;IACI;QACI,6DAA6D;QAC7D,cAAc;IAClB;;IAEA;QACI,0BAA0B;IAC9B;AACJ","sourcesContent":["body {\r\n    margin: 0;\r\n    padding: 0;\r\n    background-color: #141414;\r\n    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\r\n    height: 100vh;\r\n    overflow: hidden;\r\n    position: relative;\r\n}\r\n\r\n/* ================================== */\r\n/*  */\r\n/* ================================== */\r\n\r\n.avatar-setup {\r\n    width: 100%;\r\n    height: 100vh;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\r\n    color: white;\r\n    padding: 20px;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.setup-container {\r\n    background: rgba(0, 0, 0, 0.8);\r\n    border-radius: 15px;\r\n    padding: 30px;\r\n    max-width: 500px;\r\n    width: 100%;\r\n    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);\r\n}\r\n\r\n.setup-title {\r\n    text-align: center;\r\n    margin-bottom: 30px;\r\n    font-size: 24px;\r\n    background: linear-gradient(45deg, #ff6b6b, #4ecdc4);\r\n    -webkit-background-clip: text;\r\n    -webkit-text-fill-color: transparent;\r\n    background-clip: text;\r\n}\r\n\r\n.setup-form {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 20px;\r\n}\r\n\r\n.form-group {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 8px;\r\n}\r\n\r\n.form-group label {\r\n    font-weight: bold;\r\n    font-size: 14px;\r\n    color: #ccc;\r\n}\r\n\r\n.form-group input {\r\n    background: rgba(255, 255, 255, 0.1);\r\n    border: 1px solid rgba(255, 255, 255, 0.3);\r\n    border-radius: 8px;\r\n    padding: 10px;\r\n    color: white;\r\n    font-size: 14px;\r\n}\r\n\r\n.form-group input:focus {\r\n    outline: none;\r\n    border-color: #4ecdc4;\r\n    box-shadow: 0 0 10px rgba(78, 205, 196, 0.3);\r\n}\r\n\r\n.avatar-selection {\r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));\r\n    gap: 10px;\r\n    margin-top: 10px;\r\n}\r\n\r\n.avatar-option {\r\n    background: rgba(255, 255, 255, 0.1);\r\n    border: 2px solid transparent;\r\n    border-radius: 10px;\r\n    padding: 15px;\r\n    cursor: pointer;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    gap: 8px;\r\n    transition: all 0.3s ease;\r\n    position: relative;\r\n}\r\n\r\n.avatar-option:hover:not(.locked) {\r\n    background: rgba(255, 255, 255, 0.2);\r\n    transform: translateY(-2px);\r\n}\r\n\r\n.avatar-option.selected {\r\n    border-color: #4ecdc4;\r\n    background: rgba(78, 205, 196, 0.2);\r\n}\r\n\r\n.avatar-option.locked {\r\n    opacity: 0.5;\r\n    cursor: not-allowed;\r\n}\r\n\r\n.avatar-emoji {\r\n    font-size: 24px;\r\n}\r\n\r\n.avatar-name {\r\n    font-size: 12px;\r\n    color: #ccc;\r\n}\r\n\r\n.unlock-level {\r\n    position: absolute;\r\n    top: 5px;\r\n    right: 5px;\r\n    background: #ff6b6b;\r\n    color: white;\r\n    font-size: 10px;\r\n    padding: 2px 6px;\r\n    border-radius: 10px;\r\n}\r\n\r\n.preview-section {\r\n    background: rgba(255, 255, 255, 0.05);\r\n    border-radius: 10px;\r\n    padding: 20px;\r\n    text-align: center;\r\n}\r\n\r\n.avatar-preview {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    gap: 15px;\r\n}\r\n\r\n.preview-avatar {\r\n    font-size: 60px;\r\n    cursor: pointer;\r\n    transition: transform 0.3s ease;\r\n}\r\n\r\n.preview-avatar:hover {\r\n    transform: scale(1.1);\r\n}\r\n\r\n.preview-avatar.bouncing {\r\n    animation: gentleBounce 2s ease-in-out infinite;\r\n}\r\n\r\n/*  */\r\n@keyframes gentleBounce {\r\n    0%, 100% {\r\n        transform: translateY(0);\r\n    }\r\n    50% {\r\n        transform: translateY(-10px);\r\n    }\r\n}\r\n\r\n/*  */\r\n.preview-avatar.level-up {\r\n    animation: levelUpBounce 1s ease-out;\r\n}\r\n\r\n@keyframes levelUpBounce {\r\n    0%, 20%, 50%, 80%, 100% {\r\n        transform: translateY(0);\r\n    }\r\n    40% {\r