 color: white;\r\n    font-size: 12px;\r\n}\r\n\r\n.user-name {\r\n    font-weight: bold;\r\n    color: #4ecdc4;\r\n}\r\n\r\n.user-level {\r\n    background: linear-gradient(45deg, #ff6b6b, #4ecdc4);\r\n    padding: 4px 8px;\r\n    border-radius: 10px;\r\n    font-size: 10px;\r\n    font-weight: bold;\r\n}\r\n\r\n.user-xp {\r\n    color: #ccc;\r\n}\r\n\r\n.self-avatar {\r\n    position: absolute;\r\n    bottom: 80px;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    font-size: 2em;\r\n    z-index: 100;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n}\r\n\r\n.self-avatar.walking.bouncing .avatar-character {\r\n    animation: gentleFloat 3s ease-in-out infinite;\r\n}\r\n\r\n.avatar-character {\r\n    font-size: 3em;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.avatar-shadow {\r\n    width: 40px;\r\n    height: 8px;\r\n    background: radial-gradient(ellipse, rgba(0,0,0,0.3) 0%, transparent 70%);\r\n    border-radius: 50%;\r\n    margin-top: 5px;\r\n    animation: shadowPulse 3s ease-in-out infinite;\r\n}\r\n\r\n/*  */\r\n@keyframes gentleFloat {\r\n    0%, 100% { \r\n        transform: translateY(0px);\r\n    }\r\n    50% { \r\n        transform: translateY(-15px);\r\n    }\r\n}\r\n\r\n@keyframes shadowPulse {\r\n    0%, 100% { \r\n        transform: scale(1);\r\n        opacity: 0.3;\r\n    }\r\n    50% { \r\n        transform: scale(0.8);\r\n        opacity: 0.2;\r\n    }\r\n}\r\n\r\n/* XP */\r\n.xp-events {\r\n    position: absolute;\r\n    top: 80px;\r\n    right: 20px;\r\n    z-index: 150;\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 10px;\r\n}\r\n\r\n.xp-popup {\r\n    background: rgba(78, 205, 196, 0.9);\r\n    color: white;\r\n    padding: 8px 12px;\r\n    border-radius: 20px;\r\n    font-size: 12px;\r\n    font-weight: bold;\r\n    animation: xpPop 3s ease-out forwards;\r\n    box-shadow: 0 4px 15px rgba(78, 205, 196, 0.3);\r\n}\r\n\r\n.xp-popup.encounter {\r\n    background: rgba(255, 107,